@import url("https://fonts.googleapis.com/css2?family=Qwitcher+Grypen:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@400..800&family=Noto+Sans+KR:wght@100..900&display=swap");

* {
	margin: 0;
	padding: 0;
	color: #fff;
}

a {
	text-decoration: none;
	color: #000;
}

ul,
ol {
	list-style: none;
}

body {
	background-color: #222;
	position: relative;
}

.app {
	width: 100%;
	height: 100%;
	position: relative;
}

header {
	position: absolute;
	left: 0;
	top: 0;
}
.main {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	margin: 0 auto;
}
