.main {
	width: 500px;
	height: auto;
	padding: 40px 0 50px;
	box-sizing: border-box;
	position: absolute;
	top: 100px;
}

.main .wrap {
	width: 100%;
	height: auto;
	display: flex;
	flex-flow: row wrap;
	align-items: flex-start;
	justify-content: space-between;
	margin: 0 0 40px;
}

.main .wrap .top {
	width: 500px;
	height: 50px;
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: center;
}

.main .wrap .top .profile-photo {
	width: 50px;
	height: 50px;
	overflow: hidden;
	margin: 0 15px 0 0;
}
.main .wrap .top .profile-photo img {
	width: 50px;
	height: 50px;
	object-fit: cover;
	border-radius: 25px;
}

.main .wrap .top h3 {
	line-height: 50px;
}

.main .wrap .picture {
	width: 500px;
	height: auto;
	margin: 20px 0 0 0;
}

.main .wrap .picture img {
	width: 100%;
	height: 100%;
	border-radius: 10px;
}

.main .wrap .like {
	width: 100%;
	height: 50px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 5px;
}

.main .wrap .like p {
	font-size: 17px;
}

.main .wrap .like .likeSet {
	cursor: pointer;
	line-height: 17px;
	font-weight: 700;
}

.main .wrap .name {
	width: 100%;
	height: auto;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;
	margin: 0 0 30px 5px;
}

.main .wrap .name h3 {
	margin: 0 10px 0 0;
	line-height: 17px;
}
.main .wrap .name p {
	line-height: 17px;
}
