.sub-header {
	width: 100%;
	height: 100vh;
	position: relative;
}

.sub-header .story {
	width: 500px;
	height: 100px;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	margin: auto;
	display: flex;
	z-index: 20;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;
}

.sub-header .story img {
	width: 80px;
	height: 80px;
	border-radius: 40px;
}

.sub-header .story-detail {
	position: fixed;
	width: 100%;
	height: 100%;
	z-index: 100;
	background-color: rgba(0, 0, 0, 0.3);
	display: flex;
	justify-content: center;
	display: none;
}
.sub-header .story-detail img {
	width: auto;
	height: 100%;
	object-fit: contain;
}
.sub-header .story-detail .close {
	width: 30px;
	height: 30px;
	text-align: center;
	position: absolute;
	top: 10px;
	font-size: 25px;
	font-weight: 600;
	cursor: pointer;
	font-family: "Baloo Paaji 2", sans-serif;
	font-optical-sizing: auto;
	font-weight: 600;
	font-style: normal;
}

.story-detail.block {
	display: flex;
}
