header {
	width: 300px;
	height: 100%;
	position: fixed;
	display: flex;
	flex-flow: column nowrap;
}

header h1 {
	width: 100%;
	height: 100px;
	padding: 30px 0;
	/* border: 1px solid #fff; */
	display: flex;
	justify-content: center;
	align-items: center;
}

header h1 a {
	color: #fff;
	font-family: "Qwitcher Grypen", cursive;
	font-weight: 600;
	font-style: normal;
	font-size: 70px;
}

header .menu {
	position: absolute;
	width: 100%;
	height: 150px;
	font-family: "Noto Sans KR", sans-serif;
	font-optical-sizing: auto;
	font-weight: 500;
	font-style: normal;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
	align-items: center;
	top: 0;
	left: 0;
	bottom: 0;
	margin: auto;
}
header .menu .menu-detail {
	width: 150px;
	text-align: center;
	line-height: 40px;
	font-size: 20px;
	border-top: 1px solid #fff;
	border-bottom: 1px solid #fff;
}
